<script>
import HeaderComponent from "@/components/Header1/header1.vue";
export default {
  name: "viewDetailComponent",
  components: { HeaderComponent },
  data() {
    return {
      detail: {},
      files: [],
      person: [],
      signOff_Detail: [],
      signOff_Head: [],
      supplier: [],
      workType: [],
    };
  },
  methods: {
    async getSignOffDetail(apply_Pk) {
      try {
        const res = await this.$store.dispatch("QryDailyDetail", apply_Pk);
        if (res.data.isOK) {
          const data = res.data.data;
          this.detail = data.detail;
          this.person = data.person || [];
          this.supplier = data.supplier;
          this.files = data.files;
          this.signOff_Head = data.signOff_Head;
          this.signOff_Detail = data.signOff_Detail;
          this.workType = data.workType;
        }
      } catch (err) {
        console.log(err);
      }
    },
    async dailyWorkApplyDownloadPdf(file_Id, file_Name) {
      try {
        const res = await this.$store.dispatch(
          "dailyWorkApplyDownloadPdf",
          file_Id
        );
        const href = URL.createObjectURL(res.data);
        const link = document.createElement("a");
        link.href = href;
        link.setAttribute("download", `${file_Name}.pdf`);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(href);
      } catch (err) {
        console.log(err);
      }
    },
  },
  mounted() {
    const apply_Pk = this.$route.params.apply_Pk;
    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);
    this.$root.$children[0].sidebarSwitch = false;
    this.getSignOffDetail(apply_Pk);
  },
};
</script>

<template src="./viewDetail.html"></template>
